










import XButton from 'vux/src/components/x-button/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

export default {

components: { XButton, Icon },

data() {
return {
is_dis_sel: 0,
is_con_sel: 1
}
},

computed: {},

beforeMount () {},

mounted () {
this.$refs.content_box.style.height = document.documentElement.clientHeight - 96 + 'px';
},

methods: {
goBack() {
this.$router.go(-1);
},

dis_person() {
this.is_dis_sel = 0;
this.is_con_sel = 1;
},

con_person() {
this.is_dis_sel = 1;
this.is_con_sel = 0;
},

next_step() {
this.$router.push('/ids/create-user');
}
},

watch: {}

}




